import React, { useEffect, useState } from 'react';
import { Link, useHistory, useParams } from 'react-router-dom';
import { BsArrowLeftShort } from "react-icons/bs";
import api from '../services/api';
import styled from 'styled-components';

import { 
    getLogin,
    //setApartamentoAtual,
} from '../utils/utils-context';

export default function Servicos() {

    const { id }  = useParams();
    const [login] = useState(getLogin());
    const history = useHistory();
    const [ apartamentos, setApartamentos] = useState([]);
    const [ apartamento, setApartamento] = useState(null);

    useEffect(() => {
        if (login == null) history.push({ pathname: `/${id}` });
        api.get(`/api/rooms/${login.uuid}`).then(response => {
            setApartamentos(response.data);
        });
    },[]);

    const onVoltarClick = () => {
        if (apartamento) {
            setApartamento(null);
            return;
        }
        history.push({ pathname: `/menu/${id}` });     
    }

    const onApartamentoClick = (ap) => {
        setApartamento(ap);
    }

    function AtualizaStatusSuite() {
        console.log('>>>>>>', apartamento);
        return '';
    }

    function Apartamentos() {
        return (
            <Apartamento>
                {
                    apartamentos.map((ap, i) => {
                            const style = ap.SitAtual == 'O' ? 
                                "botao-apartamento-ocupado" : 
                                "botao-apartamento-livre";
                            return (
                                <a className={style}
                                    key={i}
                                    href="#" 
                                    onClick={ () => onApartamentoClick(ap) } 
                                    style={{color:'white'}}>{ap.id}
                                    </a> 
                            )
                        }
                    )
                }
            </Apartamento>
        );
    }

    return (
        <div className="rooms">
            <div className="header">
                <div  className="header-content">
                    <Link to="#" onClick={onVoltarClick}>
                        <BsArrowLeftShort type="button" className="seta" />
                    </Link>               
                </div>
                <h1 style={{ fontSize: 22  }}>
                    {
                        apartamento ? 'Suíte ' + apartamento.id : 'Suítes'
                    }
                </h1>
            </div>
            {
              apartamento ? <AtualizaStatusSuite /> : <Apartamentos />
            }            
        </div>
    );
    
}

const Apartamento = styled.div`
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-top: 40px;
  `;

